.sidebar {
	position: relative;
	z-index: 1;
	display: flex;
	flex-direction: column;
	background: $white;
	box-shadow: -3px 5px 4px 5px rgba($black, 0.2);
	word-break: break-word;
	@include md {
		box-shadow: none; }
	&__header,
	&__footer {
		flex: 0 0 auto; }
	&__header {
		overflow: hidden;
		&-row {
			border-bottom: 1px solid rgba($black, 0.1);
			@include clr; }
		&-col {
			float: left;
			padding: 15px 25px;
			width: 50%;
			font-size: 16px;
			@include xs {
				width: 100%; }
			&:not(:last-child) {
				border-right: 1px solid rgba($black, 0.1);
				@include xs {
					border-bottom: 1px solid rgba($black, 0.1);
					border-right: 0; } } } }
	&__footer {
		padding: 15px 0;
		border-top: 1px solid rgba($black, 0.1);
		background: #d2edf6; }
	&__total {
		color: $darkGray;
		font-size: 14px;
		line-height: 16px;
		&-list {
			&:not(:first-child) {
				border-top: 1px solid rgba(#92d4e9, 0.3); } }
		&-item {
			@include clr;
			&:not(:first-child) {
				border-top: 1px dashed rgba(#92d4e9, 0.3); } }
		&-label,
		&-value {
			position: relative;
			float: left;
			padding: 3px 25px;
			width: 50%;
			@include text-overflow; }
		&-value {
			text-align: right; } }
	&__body {
		overflow: auto;
		flex: 1 1 auto; }
	&__item {
		border-bottom: 1px solid rgba($black, 0.1); }
	&__discount-icon {
		position: absolute;
		top: 50%;
		right: 0;
		margin: -8px 5px 0 0;
		width: 16px;
		fill: currentColor; } }

.sidebar-item {
	overflow: hidden;
	padding: 10px 25px;
	&.is-highlight {
		background: #eee; }
	&__row {
		margin: 0 -5px;
		@include clr; }
	&__col {
		float: left;
		padding: 5px;
		width: 50%;
		.sidebar-item__col {
			padding-top: 0;
			padding-bottom: 0; } }
	&__value,
	&__sub {
		position: relative;
		padding: 3px 0;
		font-size: 14px;
		line-height: 16px;
		@include xs {
			font-size: 13px; }
		&.is-hidden {
			display: none; } }
	&__sub {
		color: #ababab; }
	&__discount-icon {
		position: absolute;
		top: 50%;
		left: 100%;
		margin: -8px 0 0 3px;
		width: 16px;
		fill: currentColor; } }
